import '../i18n/i18n';

import React from "react"
import FadedText from "../components/elements/FadedText";
import Navigation from "../components/layout/Navigation";
import {SEO} from "../components/seo";
import CareerForm from "../components/layout/Career/Career-form";
import {ApolloClient, InMemoryCache, ApolloProvider, HttpLink, from} from '@apollo/client';

import {onError} from "@apollo/client/link/error";

const errorLink = onError(({ graphqlErrors, networkError }) => {
    if(graphqlErrors) {
        graphqlErrors.map(({ message, location, path }) => {
            alert(`GraphQL Error ${message}`);
        });
    }
});

const link = from([
    errorLink,
    new HttpLink({
        uri: 'https://wp.kalmars.lv/wordpress/graphql/',
        fetch
    })
])


const client = new ApolloClient({
    link: link,
    cache: new InMemoryCache()
});

export default function ContactUs() {
    return (
        <ApolloProvider client={client}>
            <main className="main-wrapper">
                <Navigation pageNr={'2ET9W'}/>
                <FadedText text={'career@kalmars.lv'} direction="bottom"/>

                <section className="min-h-screen relative">
                    <CareerForm/>
                </section>
            </main>
        </ApolloProvider>

    )
}
export const Head = () => <SEO title={"Career"} description={'Are you looking for stable work place? We are looking for React, Laravel, React Native, Vue, Unity, Unreal engine developers!'}/>

